@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  background-color: whitesmoke;
  font-family: "Rajdhani", sans-serif !important;
}

.header-container {
  margin-bottom: 3rem;
  height: 90vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-image: url("https://images.unsplash.com/photo-1598515213381-80d77efcdf32?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=750&q=80"), linear-gradient(to right, #434343 0%, black 100%);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  mix-blend-mode: multiply;
  box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
}

.header-container h1 {
  color: whitesmoke;
  padding: 2rem;
  background-color: rgba(28, 29, 29, 0.63);
}

.categories-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.categories-container button {
  background-color: #f98d09;
  color: #151414;
  border-radius: 2rem;
  padding: 0.5rem 1rem;
  border: none;
  margin: 1rem;
  font-family: "Rajdhani", sans-serif !important;
  font-size: larger;
}

.categories-container button:hover, .categories-container button:focus {
  background-color: #551414;
  color: whitesmoke;
}

.meals-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: auto auto auto;
      grid-template-columns: auto auto auto;
  grid-column-gap: 3rem;
  grid-row-gap: 3rem;
  width: 100vw;
  padding: 1rem;
  margin-bottom: 3rem;
}

.card {
  box-shadow: rgba(17, 17, 26, 0.05) 0px 4px 16px, rgba(17, 17, 26, 0.05) 0px 8px 32px;
  width: 22rem;
  background-color: white;
  padding: 1rem;
}

.card .card-img {
  width: 20rem;
  margin-bottom: 0.5rem;
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em, rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
}

.card .card-title {
  font-size: 18px;
  text-align: center;
}

.footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to bottom, #4e4026 0%, #382812 40%, #353030 150%), linear-gradient(to top, rgba(77, 39, 18, 0.4) 0%, rgba(84, 55, 30, 0.25) 200%);
  background-blend-mode: multiply;
  color: whitesmoke;
  padding: 2rem;
  box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
}

.footer .footerContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 10vh;
}

.footer .iconsContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.footer .iconsContainer .icon {
  color: whitesmoke;
  margin: 1rem;
}

@media (max-width: 576px) {
  .categories-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .meals-container {
    -ms-grid-columns: auto;
        grid-template-columns: auto;
  }
  .header-container h1 {
    font-size: large;
    text-align: center;
  }
  .card {
    width: 15rem;
  }
  .card .card-img {
    width: 13rem;
  }
  .card .card-title {
    font-size: smaller;
  }
  .footer {
    font-size: smaller;
  }
}

@media (min-width: 576px) and (max-width: 768px) {
  .categories-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .meals-container {
    -ms-grid-columns: auto auto;
        grid-template-columns: auto auto;
  }
  .header-container h1 {
    font-size: large;
    text-align: center;
  }
  .card {
    width: 15rem;
  }
  .card .card-img {
    width: 13rem;
  }
  .card .card-title {
    font-size: medium;
  }
  .footer {
    font-size: medium;
  }
}

@media (min-width: 768px) and (max-width: 1080px) {
  .categories-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .meals-container {
    -ms-grid-columns: auto auto;
        grid-template-columns: auto auto;
  }
  .header-container h1 {
    font-size: large;
    text-align: center;
  }
  .card {
    width: 22rem;
  }
  .card .card-img {
    width: 20rem;
  }
  .card .card-title {
    font-size: large;
  }
  .footer {
    font-size: large;
  }
}
/*# sourceMappingURL=App.css.map */
